<template>
  <div id="incsim-calculator" class="app-container">
    <h3 class="title"><span class="fas fa-award"></span>Review and Approve</h3>

    <div class="app-content">

      <div class="infobox compact">
        <div class="content">

          <div v-if="approved" class="message ok">
            <b>The deal is approved.</b><br />You can go back and modify it, or click <i>Next Page</i> to proceed.
          </div>

          <div class="field">
            <div class="field__label">Sales Price</div>
            <div class="field__item">${{ salesPrice }}</div>
          </div>
          <div class="field">
            <div class="field__label">Annual Interest</div>
            <div class="field__item">{{ interest }}%</div>
          </div>
          <div class="field">
            <div class="field__label">Monthly Term</div>
            <div class="field__item">{{ monthlyTerm }}</div>
          </div>
          <div class="field">
            <div class="field__label">Down Payment</div>
            <div class="field__item">${{ downPayment }}</div>
          </div>
          <div class="field">
            <div class="field__label">Bi-Weekly Payment</div>
            <div class="field__item">${{ biWeekly }}</div>
          </div>
          <div class="field">
            <div class="field__label">Expected Bi-Weekly Net Income</div>
            <div class="field__item">${{ expectedIncome }}</div>
          </div>

        </div>
      </div>

    </div>

    <div class="app-nav">
      <router-link to="/" class="btn btn-secondary">< Back: Payment Simulator</router-link>
      <a href="#" class="btn btn-secondary" @click.prevent="approve" :class="{'disabled': approved}">✓ Approve</a>
    </div>

  </div>
</template>

<script>
import {Helper} from '../main.js';
import {bus} from './bus.js';
export default {
  name: 'ams_car_clc',
  data () {
    return {
      bus,
      Helper,
      t: Helper.Drupal.t,
      car: Helper.settings.ams_income_app.car || [],
      mode: 'weekly',
      initDownPayment: 0,
      downPayment: 0,
      salesPrice: 0,
      interest: 22,
      payments: 99,
      maxPayments: 99,
      paymentsPerYear: 26,
      biWeekly: 100,
      netIncome: 100,
      pmtValue: 0,
      data: {},
      approved: 0,
      monthlyTerm: 0,
      expectedIncome: 0,
      amtFinanced: 0,
    }
  },
  created () {
    this.downPayment = this.bus.down;
    this.biWeekly = this.bus.biweekly;
    this.payments = parseInt(this.car.payments);
    this.maxPayments = this.bus.payments;
    this.salesPrice = parseInt(this.car.sales_price);
    this.monthlyTerm = this.bus.monthlyTerm;
    this.expectedIncome = this.bus.expectedIncome;
    this.amtFinanced = this.bus.loan;
    if (typeof this.bus.fromStep == 'undefined') {
      this.$router.push({ name: 'simulator' });
    }
  },
  methods: {
    approve: function () {
      this.approved = 1;
      this.enableNext();
      this.setData({
        payments: this.payments,
        down_payment: this.downPayment,
        bi_weekly: this.biWeekly,
        monthly_term: this.monthlyTerm,
        expected_income: this.expectedIncome,
        loan: this.amtFinanced,
        car: this.car,
      });
    },
  }

}
</script>

<style lang="scss">
#incsim-calculator {
  .infobox .field__item {
    min-width: 120px;
    .vue-slider {
      min-width: 100px;
    }
  }
}
</style>
