<template>

  <div id="incsim-payment-calculator" class="app-container">
    <h3 class="title"><span class="fas fa-dollar-sign"></span>Payment Simulator</h3>

    <div class="app-content">

      <!--
      <div class="row debug">
        <span class="duck"></span>
        <code>
          <div>(expectedIncome {{ expectedIncome }} < netIncome {{ netIncome }}) && (affordPayment {{ affordPayment }} > biWeekly {{ biWeekly }})</div>
          <div>Loan: {{ loan }} = price ({{ salesPrice }}) * 1.07 - downPayment ({{ downPayment }}).</div>
          <div>min PMT: {{ minBiWeekly }}.</div>
        </code>
      </div>
      -->

      <div class="row">
        <div class="col col-12">
          <div class="large">Bi-weekly, your net income is <code>${{ netIncome }}</code> and the afforded payment is <code>${{ affordPayment }}</code>.</div>
          <div class="large message info"><span class="label">You can increase the Down Payment to reduce the Bi-Weekly Payment.</span></div>
        </div>
      </div>
      <div class="row">
        <div class="col col-12 col-lg-6">
          <div class="large spread"><span class="label">Down Payment:</span> <span class="value">${{ downPayment }}</span></div>
          <vue-slider v-model="downPayment" :tooltip-formatter="val => '$' + val" :min="initDownPayment" :max="initDownPayment * 10" contained="true"></vue-slider>
        </div>
        <div class="col col-12 col-lg-6">
          <div class="large spread"><span class="label">Bi-Weekly Payment:</span> <span class="value">${{ biWeekly }}</span></div>
          <div class="med spread"><span class="label">Expected Bi-Weekly Net Income:</span> <span class="value">${{ expectedIncome }}</span></div>
        </div>
      </div>
    </div>

    <div class="app-nav">
      <span></span>
      <router-link v-if="termsValid" to="/approve" class="btn btn-secondary">Approved! Review Deal ></router-link>
    </div>

  </div>
</template>

<script>
import {Helper} from '../main.js';
import {bus} from './bus.js';
export default {
  name: 'ams_payment_clc',
  data () {
    return {
      bus,
      Helper,
      t: Helper.Drupal.t,
      car: Helper.settings.ams_income_app.car || [],
      user: Helper.settings.ams_income_app.user || [],
      mode: 'weekly',
      initDownPayment: 0,
      downPayment: 0,
      initBiWeekly: 0,
      biWeekly: 0,
      netIncome: 0,
      approved: 0,
    }
  },
  created () {
    this.mode = this.user.salary_frequency || 'weekly';
    if (this.mode === 'weekly') {
      this.netIncome = parseFloat(this.user.weekly_salary) * 2;
    }
    else {
      this.netIncome = parseFloat(this.user.biweekly_salary);
    }
    this.initDownPayment = this.car.down;
    this.downPayment = this.bus.down ? this.bus.down : this.car.down;
    this.biWeekly =  this.bus.biweekly ?  this.bus.biweekly : this.car.biweekly;
    this.initBiWeekly = this.car.biweekly;
    this.salesPrice = parseInt(this.car.sales_price);
    this.disableNext();
  },
  computed: {
    termsValid: function () {
      return (this.expectedIncome <= this.netIncome) && (this.affordPayment >= this.biWeekly);
    },
    expectedIncome: function () {
      return this.round((this.biWeekly * 26 / 12) * 2);
    },
    affordPayment: function () {
      return this.round(this.netIncome / 26 * 12 / 2);
    },
    selectUrl: function () {
      return '/inventory?f[0]=biweekly:(min:0,max:' + this.affordPayment + ')';
    },
    loan: function () {
      return this.round(this.salesPrice * 1.07 - this.downPayment);
    },
    minBiWeekly: function () {
      return parseInt(this.pmt(this.car.interest / 26, this.car.payments, this.loan));
    },
    monthlyTerm: function () {
      return Math.round(parseInt(this.car.payments) * 12 / 26);
    },
  },
  methods: {
    updateBus: function () {
      this.bus.down = this.downPayment;
      this.bus.netIncome = this.netIncome;
      this.bus.biweekly = this.biWeekly;
      this.bus.payments = this.car.payments;
      this.bus.monthlyTerm = this.monthlyTerm;
      this.bus.expectedIncome = this.expectedIncome;
      this.bus.fromStep = 'sim';
    },
  },
  watch: {
    minBiWeekly(val) {
      this.biWeekly = val;
    }
  },
  beforeRouteLeave: function (to, from, next) {
    this.updateBus();
    next();
  },

}
</script>

